import React, {Component} from 'react'
import loginCss from './Login.module.css'
import Input from "../components/UI/Input/Input";
import Button from "../components/UI/Button/Button";
import {isAuth, login, register} from '../modules/login'
import is from 'is_js'

class Login extends Component {

    keys = {
        email: 'email',
        password: 'password',
        passwordConfirm: 'passwordConfirm'
    }

    state = {
        isFormValid: false,
        activeForm: 'login',
        formControls: {
            [this.keys.email]: {
                value: '',
                type: 'email',
                touched: false,
                valid: false,
                label: 'Email',
                placeholder: 'Write your email',
                errorMessage: 'Введите корректный email',
                validation: {
                    required: true,
                    email: true
                }
            },
            [this.keys.password]: {
                value: '',
                type: 'password',
                touched: false,
                valid: false,
                label: 'Password',
                placeholder: 'Write your password',
                errorMessage: 'Введите корректный пароль',
                validation: {
                    required: true,
                    minLength: 6,
                    maxLength: 15
                }
            },
            [this.keys.passwordConfirm]: {
                value: '',
                type: 'password',
                touched: false,
                valid: false,
                label: 'Confirm password',
                placeholder: 'Confirm your password',
                errorMessage: 'Введите корректный пароль',
                validation: {
                    required: true,
                    minLength: 6,
                    maxLength: 15
                }
            }
        }
    }

    componentDidMount() {
        if (isAuth()) {
            this.props.history.push({
                pathname: '/todos'
            })
        }
    }

    validateControl(value, validation) {
        if (!validation) {
            return true
        }

        let isValid = true

        if (validation.required) {
            isValid = value.trim() !== '' && isValid
        }

        if (validation.email) {
            isValid = is.email(value) && isValid
        }

        if (validation.minLength) {
            isValid = value.length >= validation.minLength && isValid
        }

        if (validation.maxLength) {
            isValid = value.length <= validation.maxLength && isValid
        }

        return isValid
    }

    changeHandler = (event, controlName) => {
        const value = event.target.value

        let isFormValid = true

        const names = [
            this.keys.email,
            this.keys.password
        ]

        if (this.state.activeForm === 'reg') names.push(this.keys.passwordConfirm)

        const formControls = {...this.state.formControls}
        formControls[controlName].valid = this.validateControl(value, formControls[controlName].validation)
        formControls[controlName].value = value
        formControls[controlName].touched = true

        names.forEach(name => {
            isFormValid = formControls[name].valid && isFormValid
        })

        this.setState(() => {
            return {
                isFormValid,
                formControls
            }
        })
    }

    changeActiveForm = (type) => {
        this.setState({
            activeForm: type
        })

        Object.keys(this.state.formControls).map((controlName) => {
            this.setState(state => {
                return {
                    isFormValid: false,
                    formControls: {
                        ...state.formControls,
                        [controlName]: {
                            ...state.formControls[controlName],
                            value: '',
                            touched: false
                        }
                    }
                }
            })
        })
    }

    loginHandler = (event) => {
        event.preventDefault()

        const user = {
            email: this.state.formControls[this.keys.email].value.toString().trim(),
            password: this.state.formControls[this.keys.password].value.toString().trim()
        }

        const status = login(user)

        if (status) {
            this.props.history.push({
                pathname: '/todos'
            })
        } else {
            alert('User not found!')
        }

    }

    registerHandler = (event) => {
        event.preventDefault()

        const newUser = {
            email: this.state.formControls[this.keys.email].value.toString().trim(),
            password: this.state.formControls[this.keys.password].value.toString().trim(),
            passwordConfirm: this.state.formControls[this.keys.passwordConfirm].value.toString().trim(),
        }

        const status = register(newUser)

        if (status) this.props.history.push({
            pathname: '/todos'
        })
    }

    getInput(key) {
        const control = this.state.formControls[key]

        return (
            <Input
                type={control.type}
                value={control.value}
                changeInputHandler={(e) => this.changeHandler(e, key)}
                label={control.label}
                placeholder={control.placeholder}
                valid={control.valid}
                errorMessage={control.errorMessage}
                touched={control.touched}
                shouldValidate={!!control.validation}
            />
        )
    }

    render() {

        const regBox = (
            <React.Fragment>
                {this.getInput(this.keys.passwordConfirm)}
                <Button
                    text={'Register'}
                    btnType={'btn-register'}
                    btnClick={(e) => this.registerHandler(e)}
                    disabled={!this.state.isFormValid}
                />
            </React.Fragment>
        )

        const loginBox = (
            <Button
                text={'Login'}
                btnType={'btn-login'}
                btnClick={(e) => this.loginHandler(e)}
                disabled={!this.state.isFormValid}
            />
        )

        return (
            <div className={loginCss.LoginContainer}>
                <div className={loginCss.Login}>
                    <form className={loginCss.form}>
                        <legend>
                            <span
                                className={this.state.activeForm === 'login' ? loginCss.active : null}
                                onClick={this.changeActiveForm.bind(this, 'login')}
                            >Login</span>

                            <span
                                className={this.state.activeForm === 'reg' ? loginCss.active : null}
                                onClick={this.changeActiveForm.bind(this, 'reg')}
                            >Register</span>
                        </legend>

                        {this.getInput(this.keys.email)}

                        {this.getInput(this.keys.password)}

                        {
                            this.state.activeForm === 'reg' ? regBox : loginBox
                        }

                    </form>
                </div>
            </div>
        )
    }
}

export default Login
