import React, {Component} from 'react'
import AlertContext from './AlertContext'
import alertCss from './Alert.module.css'

class Alert extends Component {

    myRef = React.createRef()

    state = {
        isShow: false,
        type: '',
        message: '',
        deleteId: null,
        editId: null,
        editText: ''
    }

    info = {
        todoId: null,
        todoText: null
    }

    maxLength = 100
    minLength = 5

    componentDidUpdate() {
        if (this.state.type === 'edit' && this.myRef && this.info.todoText) {
            this.myRef.current.innerHTML = this.maxLength - this.info.todoText.length
        }
    }

    showDeleteAlert = (id) => {
        this.info.todoId = id

        this.setState(() => {
            return {
                isShow: true,
                type: 'delete'
            }
        })
    }

    showEditAlert = (id, title) => {
        this.info = {
            todoId: id,
            todoText: title
        }

        this.setState(() => {
            return {
                isShow: true,
                type: 'edit'
            }
        })
    }

    changeTextHandler = (event) => {
        const value = event.nativeEvent.target.value.toString().trim()

        this.info.todoText = value
        this.myRef.current.innerHTML = this.maxLength - value.length
    }

    completeEdit = () => {
        this.info = {
            todoId: null,
            todoText: null
        }

        this.setState(() => {
            return {
                isShow: false,
                type: '',
                editId: null
            }
        })
    }

    triggerEdit = () => {
        const length = this.info.todoText.length

        if (length < this.minLength || length > this.maxLength) {
            alert(`Invalid string length! The correct length is ${this.minLength} to ${this.maxLength} characters.`)
            return
        }

        this.setState(() => {
            return {
                editId: this.info.todoId,
                editText: this.info.todoText
            }
        })
    }

    triggerDelete = () => {
        this.setState(() => {
            return {
                deleteId: this.info.todoId
            }
        })
    }

    completeDelete = () => {
        this.info.todoId = null

        this.setState(() => {
            return {
                isShow: false,
                type: '',
                deleteId: null
            }
        })
    }

    render() {
        return (
            <React.Fragment>
                <AlertContext.Provider value={{
                    state: this.state,
                    showDeleteAlert: this.showDeleteAlert,
                    completeDelete: this.completeDelete,
                    showEditAlert: this.showEditAlert,
                    completeEdit: this.completeEdit
                }}>
                    {this.props.children}
                </AlertContext.Provider>
                {
                    this.state.isShow && this.state.type === 'delete'
                        ? <div className={alertCss.container}>
                            <div className={alertCss.delete}>
                                <h2 className={alertCss.delete__title}>Delete item?</h2>
                                <div className={alertCss.delete__buttons}>
                                    <button
                                        className={alertCss.delete__button}
                                        onClick={this.triggerDelete}
                                    >YES
                                    </button>
                                    <button
                                        className={`${alertCss.delete__button} ${alertCss.delete__buttons_no}`}
                                        onClick={this.completeDelete}
                                    >NO
                                    </button>
                                </div>
                            </div>
                        </div>
                        : null
                }

                {
                    this.state.isShow && this.state.type === 'edit'
                        ? <div className={alertCss.container}>
                            <div className={alertCss.edit}>
                                <h2 className={alertCss.edit__title}>Edit text</h2>
                                <textarea
                                    className={alertCss.edit__area}
                                    defaultValue={this.info.todoText}
                                    onChange={this.changeTextHandler}
                                />
                                <span>
                                Characters limit:
                                    <p className={alertCss.edit__count} ref={this.myRef}/>
                            </span>
                                <div className={alertCss.edit__buttons}>
                                    <button
                                        className={alertCss.edit__button + ' ' + alertCss.edit__button_cancel}
                                        onClick={this.completeEdit}
                                    >CANCEL
                                    </button>
                                    <button
                                        className={alertCss.edit__button}
                                        onClick={this.triggerEdit}
                                    >SAVE
                                    </button>
                                </div>
                            </div>
                        </div>
                        : null
                }
            </React.Fragment>
        )
    }
}

export default Alert
